.edit-page {
    width: 480px;
    margin: 50px auto;
    form {
      position: relative;
      border: 2px solid black;
      padding: 15px;
      input {
        margin: 10px;
        height: 40px;
        padding: 5px;
        font-size: 16px;
        border:0;
        border-bottom: 1px solid white;
        background-color: transparent;
        outline:0;
        color:white;
        &::placeholder{color:white}
      }
      button {
        width: 250px;
        height: 50px;
        border: 1px solid black;
        margin: 10px;
        background-color: transparent;
        font-size: 18px;
        border-radius: 5px;
        transition: all 0.3s ease-in-out;
        font-weight: bold;
        align-self: center;
        &:hover {
          background-color: #3498db;
          color: white;
        }
      }
      Link i, i{
          font-size: 25px;
          cursor: pointer;
      }
      .remove{
          position: absolute;
          top:-40px;
          right:0;
      }
      .back{
          position: absolute;
          top:-40px;
          left:0;
      }
      h3{
          position: absolute;
          left:50%;
          top:-45px;
          margin:0;
          padding: 0;
          transform: translateX(-50%);
          font-weight: bold;
      }
      img{
          width: 150px;
          align-self: center;
          border-radius: 50%;
      }
    }
  }
  
  @media (max-width:380px) {
    .edit-page {
      width: 350px;
      form{
        h3{
          font-size: 20px;
        }
      }
      input,select{
        font-size: 14px;
      }
    }
  }
  